import React, { useState } from "react";
import './Quini.css'; // Aqu importamos los estilos de Quini.js.

const Quini = () => {
  // Estado para guardar los nmeros ganadores
  const [numbers, setNumbers] = useState([]);
  
  // Funcin para generar los nmeros aleatorios
  const generateNumbers = () => {
    let selectedNumbers = [];
    while (selectedNumbers.length < 6) {
      const randomNumber = Math.floor(Math.random() * 46);
      if (!selectedNumbers.includes(randomNumber)) {
        selectedNumbers.push(randomNumber);
      }
    }
    setNumbers(selectedNumbers);
  };

  return (
    <div className="quini-container"> {/* Contenedor especfico para Quini.js */}
      <div className="header">
        <h1 className="title">Quini 6! Diseo web</h1>
        <p>Sigue la suerte y apret la palanca!</p>
      </div>
      <div className="numbers-display">
        {numbers.length > 0 ? (
          numbers.map((number, index) => (
            <div key={index} className="number-card">
              {number}
            </div>
          ))
        ) : (
          <p className="instructions">Pulsa la palanca para ver los nmeros ganadores!</p>
        )}
      </div>
      <button className="lever" onClick={generateNumbers}>Apret la palanca!</button>
    </div>
  );
};

export default Quini;
